<template>
  <div id="app" ref="app" class="">
		<div class="header-toolbar">
      <span class="hamo-ver">Ver:1.01 - HAMO</span>
			<span class="toggleBtn" @click="bgToggle()">{{btn}}</span>
		</div>
		<Partylist v-model="mode"></Partylist>
		<div id="handle" class="handle">┛</div>	
  </div>
</template>

<script>
import Partylist from './components/Partylist.vue'
export default {
  name: 'app',
	components: {
		Partylist
	},
	data: () => ({
		mode: "",
		btn: "□"
	}),
	methods: {
		bgToggle() {
			if (!this.$refs.app.classList.contains("trans")) {
				this.$refs.app.classList.add("trans");
				this.btn = "■"	
			} else {
				this.$refs.app.classList.remove("trans");
				this.btn = "□"
			}
		}
	}	
}
</script>

