<template>
	<div class="partylist-container">
		<Api v-model="party"/>
		<Meter v-if="p1" v-model="p1"></Meter>
		<Meter v-if="p2" v-model="p2" class="no2"></Meter>
		<Meter v-if="p3" v-model="p3" class="no3"></Meter>
		<Meter v-if="p4" v-model="p4" class="no4"></Meter>
		<Meter v-if="p5" v-model="p5" class="no5"></Meter>
		<Meter v-if="p6" v-model="p6" class="no6"></Meter>
		<Meter v-if="p7" v-model="p7" class="no7"></Meter>
		<Meter v-if="p8" v-model="p8" class="no8"></Meter>
	</div>
</template>


<script>
import Api from './Api.vue'
import Meter from './Meter.vue'

export default {
	name: "Partylist",
	components: {
		Api,
		Meter
	},
	data: () => ({
		party: [],
		p1: null,
		p2: null,
		p3: null,
		p4: null,
		p5: null,
		p6: null,
		p7: null,
		p8: null,
	}),
	
	watch: {
		party() {
			this.p1 = this.party[0]
			this.p2 = this.party[1]
			this.p3 = this.party[2]
			this.p4 = this.party[3]
			this.p5 = this.party[4]
			this.p6 = this.party[5]
			this.p7 = this.party[6]
			this.p8 = this.party[7]
		}
	},
	
	props: ["value"],
	computed: {
		mode: {
			get() {
				return this.value
			},
			set(v) {
				this.$emit("input", v)
			}
		}
	},
	mounted() {
	}
}
</script>

<style>
div.partylist-container {
	margin: 13px;
}

div.partylist-container {
   width: 100%;
   display: flex;
   justify-content: flex-start;
}
div.meter-container {
  flex: 1 0 170px;
}

@media screen and (max-width: 450px) {
	div.partylist-container {
		width: 350px;
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
	}
	div.meter-container {
		flex: 1 0 170px;
	}
	.no2 {
		margin-top: 30px;
	}
	.no3, .no5, .no7 {
		margin-top: -30px;
	}
}


</style>
