<template>
  <div class="meter-container">
    <div ref="container" class="wrapper"></div>
		<div class="meter-dps text-border" v-html="dpsDigit"></div>
    <div class="meter-dps-label">DPS</div>
		<div class="meter-text-info">
			<p class="meter-job text-border">{{job}}</p>
			<p ref="charname" class="meter-name text-border" @click="toAnon">{{name}}</p>
		</div>
  </div>
</template>

<script>
/*
	v-model is the percentage of the arc length.
*/
import Snap from "snapsvg";
export default {
  props: ["value", "label"],

  computed: {
    val: {
      get() {
        return this.value
      },
      set(v) {
				if (v) this.update()
        this.$emit("input", v)
      }
		},

		dpsDigit: {
			get() {
				if (!this.value.dps) return 0
				let label = "",
						d1, d2
				[d1, d2] = this.value.dps.split(".")
				label = d1 + "." + "<span style='font-size: 0.6rem'>" + d2 + "</span>"
				return label
			}
		},

		job: {
			get() {
				return this.value.job
			}
		},
		name: {
			get() {
				return this.value.name
			}
		}
	},
	
	watch: {
		val() {
			this.update()
		}
	},

  data: () => ({
		uid: "",
    arc: null,
		pluginsAdded: false,
		disalbe: false,
  }),

  methods: {

    init() {
      let w = this.$refs.container.clientWidth,
				h = this.$refs.container.clientHeight,
				initval = {
        	w: w,
        	h: h,
        	start: 270,
        	end: 270.01,
        	sw: 20,
      	}
			let p1 = Snap(w, h).remove()
			initval.stroke = "#8898aa"
			p1.arc(initval)
			let path = p1.select("path")
			this.uid = path.id
			path.attr("id", this.uid)
			p1.prependTo(this.$refs.container)
    },

		toAnon() {
			let el = this.$refs.charname;
			el.classList.toggle("anon")	
		},

    update() {
			if (!this.val) return
			if (this.val.name === "Limit Break") return
			
      let degree = 270 - (2.7 * parseInt(this.val.ratio))
     	if (degree - (270%360 === 0)) degree += 0.01

			let a = Snap.select("#"+this.uid);
      a.stop().animate(
        {
          start: degree
        },
        300
      );
    },

    addSnapPlugin() {
      Snap.plugin(function(Snap, Element, Paper, glob) {
        /**
         * w: boundingbox width
         * h: boundingbox height
         * start: 0 degree is 3 oclock, rotate to counter clockwise direction when increase this value
         * end: same start.
         * sw: stroke weight, this value is percentage of the width value.
         * 		 ex) stroke-width will be 16px when this w(boundingbox width) is 160px and specify 10 to this value.
         */
        Paper.prototype.arc = function(o) {
          let arc = this.path();
					arc.attr("shapeType", "arc");
					let shadow = this.filter(Snap.filter.shadow(1, 1, .9, "#000000", 1))

					arc.attr({filter: shadow})

					o.w -= 10
					o.h -= 10

          o.sw = parseInt((o.w / 100) * o.sw)
          o.w = parseInt(o.w)
          o.h = parseInt(o.h)
          o.w /= 2
          o.h /= 2
					o.r = o.w - o.sw / 2
					
					o.w +=5
					o.h += 5
					
          arc.attr(o)
          return arc;
        };

        let arcPath = "M{w},{h}U{r},{start},{end}",
          param = { w: 0, h: 0, r: 0, start: 0, end: 0, sw: 0 }

        function drawArc() {
          if (this.type !== "path" || this.attr("shapeType") !== "arc") return
          for (let i in param) {
            param[i] = this.attr(i) || 0;
          }
          if (param.start - (param.end % 360) === 0) param.end -= 0.01
					this.attr("path", Snap.format(arcPath, param))
					this.attr("strokeWidth", param.sw)
					this.attr("class", "arc-container")
					this.attr("fill", "none");

        }
        eve.on("snap.util.attr.x", drawArc)(1)
        eve.on("snap.util.attr.y", drawArc)(1)
        eve.on("snap.util.attr.r", drawArc)(1)
        eve.on("snap.util.attr.start", drawArc)(1)
        eve.on("snap.util.attr.end", drawArc)(1)
        eve.on("snap.util.attr.sw", drawArc)(1)
      });
    }
  },

  mounted() {
		if (!this.value) return
    if (!this.pluginAdded) {
      this.addSnapPlugin();
      this.pluginAdded = true;
		}
		this.init();

  }
};
</script>


<style scoped>
@import url("https://fonts.googleapis.com/css?family=Michroma|Play&display=swap");

div.meter-text-info {
	display: flex;
	align-items: center;
	margin-top: -16px;
}

.meter-name {
	font-family: "Play", sans-serif;
	text-align: left;
	font-size: 12px;
	color: #bbb;
	width: 131px;
	white-space:nowrap;
	overflow: hidden;
	margin-bottom: 0;
	filter: drop-shadow(1px, 1px, 3px, rgb(0, 0, 0));
}

.meter-name.anon {
	filter: blur(3px)
}

p.meter-job {
	font-family: "Play", sans-serif;
	font-style: italic;
	font-size: 12px;
	width: 34px;
	text-align: right;
	padding-right: 5px;
	margin-bottom: 0;
	
}
.text-border{
	text-shadow:1px 1px 0 	rgba(0, 0, 0, .6),
						 -1px -1px 0 	rgba(0, 0, 0, .6),
             -1px 1px 0 	rgba(0, 0, 0, .6),
						  1px -1px 0  rgba(0, 0, 0, .6),
              0px 1px 0		rgba(0, 0, 0, .6), 
							0-1px 0 		rgba(0, 0, 0, .6),
             -1px 0 0 		rgba(0, 0, 0, .6),
						  1px 0 0			rgba(0, 0, 0, .6);
}
.meter-job span {

	text-shadow: 1px 1px 1px #000; 
}

p.btn-anon:hover {
	opacity: 0.6;
}

.meter-container {
	width: 175px;
	height: 84px;
}

.meter-dps-label {
  font-family: "Michroma", sans-serif;
	font-weight: bold;
	font-size: 11px;
	color: #666688;
	margin: -4px 0 0 37px;
}

.meter-dps {
	font-family: "Michroma", sans-serif;
	font-weight: bold;
	font-style: italic;
	font-size: 15px;
	text-align: left;
	letter-spacing: 1px;
	margin: -36px 0 0 37px;
	filter:drop-shadow(1px 1px 2px rgb(0, 0, 0, 1));
	color: #9999aa;
	width: 100px;
}

.wrapper {
  width: 70px;
  height: 70px;
}
</style>